@import '../../styles/var.scss';

.product-list {
  max-width: 750px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  height: 70vh;
  overflow-y: scroll;
  box-shadow: 0 0 4px 4px rgba(0, 0, 0, 0.25);
  padding: 30px;
  border-radius: 5px;

  @include r(1200){
    //grid-template-columns: 1fr;
  }

  @include r(576) {
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: auto;
    margin-bottom: 30px;
  }

  &::-webkit-scrollbar {
    width: 16px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: rgb(34, 37, 201);
    border: 4px solid transparent;
    border-radius: 8px;
    background-clip: padding-box;
  }


}