@import '../../styles/var.scss';

.shop {
  border-radius: 5px;
  padding: 40px;
  width: 400px;
  box-shadow: 0 0 4px 4px rgba(#000, 0.25);
  text-align: center;

  @include r(992) {
    width: 100%;
  }


  &__title {
    margin-bottom: 25px;
  }

  &__wrapper {
    display: flex;
    margin: 0 auto;
    justify-content: center;
    align-items: flex-start;
    gap: 30px;
    margin-top: 30px;

    @include r(992) {
      flex-wrap: wrap;
    }
  }

  &__list {

    @include r(992) {
      display: flex;
      align-items: flex-start;
      justify-content: center;
      flex-wrap: wrap;
      gap: 15px;
    }
  }

  &__item {
    padding: 15px;
    border: 1px solid rgba(#000, 0.5);
    border-radius: 20px;
    cursor: pointer;

    @include r(992) {
      width: 200px;
    }

    &:not(:last-child){
      margin-bottom: 15px;
    }

  }
}